import './slider'
import './menu'


console.log('Hello World!');


$(document).on("scroll", function() {

	if($(document).scrollTop()>100) {
		$("header").removeClass("large").addClass("small");
		$("img.nav__logo-img").removeClass("large").addClass("small");
		$("div.nav__redes").removeClass("large").addClass("small");

	} else {
		$("header").removeClass("small").addClass("large");
		$("img.nav__logo-img").removeClass("small").addClass("large");
		$("div.nav__redes").removeClass("small").addClass("large");
	}	
});


